import { Component, OnInit } from '@angular/core';

import { Stone } from '../stone';
import { StoneService } from '../stone.service';

@Component({
  selector: 'app-connect6',
  templateUrl: './connect6.component.html',
  styleUrls: ['./connect6.component.css']
})
export class Connect6Component implements OnInit {

  stones: Stone[];

  constructor(private stoneService: StoneService) { }

  ngOnInit() {
    this.getStones();
  }

  getStones(): void {
    this.stoneService.getStones()
        .subscribe(stones => this.stones = stones);
  }

}
