import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { Stone } from './stone';

@Injectable({
  providedIn: 'root'
})
export class StoneService {
 
  private stonesUrl = 'api/heroes';
 
  constructor(
    private http: HttpClient) { }

  getStones (): Observable<Stone[]> {
    return this.http.get<Stone[]>(this.stonesUrl)
      .pipe(
        catchError(this.handleError<Stone[]>('getStones', []))
      );
  }
  
  /**
   * HTTP 요청이 실패한 경우를 처리합니다.
   * 애플리케이션 로직 흐름은 그대로 유지됩니다.
   * @param operation - 실패한 동작의 이름
   * @param result - 기본값으로 반환할 객체
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
 
      // TODO: 리모트 서버로 에러 메시지 보내기
      console.error(error); // 지금은 콘솔에 로그를 출력합니다.
 
      // 애플리케이션 로직이 끊기지 않도록 기본값으로 받은 객체를 반환합니다.
      return of(result as T);
    };
  }
}


